body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
  background-color: #e7f1db;
}

.container {
  max-width: 600px;
  margin: auto;
  padding: 2rem;
  text-align: center;
}

h1 {
  font-size: 2rem;
  color: #2d732d;
  margin-bottom: 1rem;
}

.input-section {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-bottom: 1rem;
}

input {
  padding: 0.6rem;
  width: 60%;
  border: 1px solid #aaa;
  border-radius: 8px;
  font-size: 1rem;
}

button {
  background-color: #8bc34a;
  color: white;
  border: none;
  padding: 0.6rem 1rem;
  font-size: 1rem;
  border-radius: 8px;
  cursor: pointer;
}

button:hover {
  background-color: #7cb342;
}

.card {
  background: white;
  border-radius: 12px;
  padding: 1rem;
  margin: 1rem 0;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  text-align: left;
}

.card h3 {
  margin: 0 0 0.5rem;
}

.card ul {
  list-style: none;
  padding: 0;
}

.card ul li {
  margin: 0.2rem 0;
}

.summary {
  text-align: center;
}

.suggestion {
  font-style: italic;
  margin-bottom: 1rem;
  color: #444;
}
